import HeaderServices from '../Services/HeaderServices';
export default {
	getElements({ commit }) {
		HeaderServices.getHeader().then((response) => {
			console.log(response.data);
			commit('SET_Header', response.data);
		});
	},
	getShearch({ commit }, value) {
		HeaderServices.getShearch(value).then((response) => {
			commit('SET_Filter', response.data);

			/**    response.data.forEach(resp => {
           if (resp.subtype =="product") {
             
           }
         
         });
       * 
       */
			console.log('value after filter :' + value);
			console.log('after filter : ' + response.data);
		});
	},
};
