/* eslint-disable prettier/prettier */
import axios from "axios";
import {getHeader,getShearch} from "./HeaderAPI";
export default {
  getHeader() {
      return (
          axios.get(getHeader).then(res => {
              return res;
          })
      )
  },
  getShearch (va) {
      return axios.get(getShearch + va ).then ( res => {
          return res;
      })
  }
}