<!--eslint-disable -->
<template>
	<div class="Header">
		<div class="row AtTop">
			<div class="col-6 logo">
				<img src="../../assets/Alibaba.svg" alt="logo" />
			</div>
			<div class="col-6">
				<form class="form-inline my-2 my-lg-0">
					<i class="bi bi-search"></i>
					<input
						class="form-control mr-sm-2"
						v-model="search"
						v-on:keyup="submit"
						placeholder="What are you looking.."
						aria-label="Search"
					/>
				</form>
			</div>
		</div>
		<div v-if="visible" class="visible">
			<!-- for Exit  -->
			<button @click="close" type="button" class="close" aria-label="Close">
				<span aria-hidden="true">&times;</span>
			</button>

			<div class="searchfilter">
				<img src="../../assets/wear.webp" class="img" alt=" test" />
				<span> النص هنا </span>
			</div>
			<div class="searchfilter">
				<img src="../../assets/wear.webp" class="img" alt=" test" />
				<span> النص هنا </span>
			</div>
		</div>
		<div class="clear"></div>
		<div class="scrollmenu">
			<a class="one" href="#"> </a>
			<a href="#" v-for="Header in Headers" :key="Header.id">{{ Header.name }}</a>
		</div>
	</div>
</template>
<!--eslint-disable -->
<script>
import { mapState } from 'vuex';
export default {
	data() {
		return {
			index: 1,
			search: '',
			visible: false,
		};
	},
	components: {},
	mounted() {
		this.$store.dispatch('Header/getElements');
	},

	computed: {
		...mapState({
			Headers: (state) => state.Header.Header,
		}),
	},
	methods: {
		submit: function () {
			if (this.search == '') {
				this.visible = false;
			} else {
				this.visible = true;
			}
			this.$store.dispatch('Header/getShearch', this.search);
			console.log('values from Header:' + this.search);
		},
		close: function () {
			this.visible = false;
		},
	},
};
</script>

<style scoped>
@import url('https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css');
.Header {
	height: 50px;
	background: rgb(255, 255, 255);
}
.Header .AtTop {
	position: fixed;
	top: 0;
	background-color: #fff;
	z-index: 16;
	width: 100%;
}
.Header .logo {
	margin-top: 10px;
}
.Header img {
	max-width: 150px;
}
.Header i {
	position: absolute;

	left: 20px;
	width: 24px;
	height: 24px;

	margin-right: 15px;
}
.Header .visible {
	color: #999;
	position: fixed;
	top: 41px;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 1001;
	display: flex;
	flex-direction: row;
	background: #fff;
}
.clear {
	height: 50px;
	clear: both;
}
.Header .form-control {
	border-radius: 4.8vw;
	max-width: 250px;
	padding-left: 25px;
}
.left {
	position: absolute;
	left: 0;
}

.scrollmenu {
	overflow: auto;
	white-space: nowrap;
	text-align: center;

	margin-top: 0;
}
.scrollmenu a {
	display: inline-block;
	text-align: center;
	padding-top: 2px;
	padding-right: 10px;
	padding-bottom: 5px;
	text-decoration: none;
	color: #1f1f21;
}
.scrollmenu a:active {
	background: #ece9f2;
}
.visible .img {
	width: 30px;
}
.close {
	float: left;
	height: 10px;
	margin-top: 10px;
}
.searchfilter {
	margin-top: 60px;
	margin-right: 15px;
	text-align: right;
}
.searchfilter span {
	margin-left: 25px;
}
</style>
